.team-container {
  margin-left: 2%;
  margin-right: 2%;
}

.team-card-container {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  margin-top: 40px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-bottom: 100px;
}

.team-member {
  width: 200px;
  height: 300px;
  position: relative;
  top: 0;
  -webkit-transition: top ease 0.5s;
  -o-transition: top ease 0.5s;
  transition: top ease 0.5s;
}

.team-member:hover {
  top: -20px;
}

.team-member a img {
  height: auto;
  width: 200px;
  -webkit-transition: opacity 100ms;
  -o-transition: opacity 100ms;
  transition: opacity 100ms;
}

.team-member a:hover img{
  opacity: 0.1;
}

.team-member a span {
  position: absolute;
  height: 100%;
  width: 100%;
  text-align: center;
  top: 25%;
  color: white;
  opacity: 0;
}

.team-member a:hover span {
  opacity: 1;
}

.team-member-title {
  font-size: 16px;
}

.team-member-info {
  font-size: 16px;
}

@media only screen and (max-width: 825px) {
    .team-container {
      -ms-flex-pack: center;
          justify-content: center;
    }

    .team-card-container {
      -ms-flex-pack: center;
          justify-content: center;
    }

    .team-member {
      margin-right: 2%;
      margin-bottom: 10%;
    }
}


