.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 40px;
  border-bottom: 4px solid #eb4a9d;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  -webkit-animation: fade-in 1.3s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
          animation: fade-in 1.3s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.navbar-logo {
  height: 60px;
  width: auto;
  -webkit-animation: float 4s ease-in-out infinite;
          animation: float 4s ease-in-out infinite;
}

.navbar-item {
  margin-left: 20px;
  font-size: 22px;
  position: relative;
  top: 0px;
  -webkit-transition: top ease 0.25s;
  -o-transition: top ease 0.25s;
  transition: top ease 0.25s;
}

.navbar-item:hover {
  top: -5px;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes float {
  0% {
    -webkit-box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
            box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  50% {
    -webkit-box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
            box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
    -webkit-transform: translatey(-10px);
            transform: translatey(-10px);
  }
  100% {
    -webkit-box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
            box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}

@keyframes float {
  0% {
    -webkit-box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
            box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  50% {
    -webkit-box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
            box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
    -webkit-transform: translatey(-10px);
            transform: translatey(-10px);
  }
  100% {
    -webkit-box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
            box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}

@media only screen and (max-width: 825px) {
    .navbar, .navbar-links {
      -ms-flex-pack: center;
          justify-content: center;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
    }
}


